import React, { useState, useEffect } from "react";
import axios from "axios";
import { jwtDecode as jwt_decode } from "jwt-decode";
import NavBar from '../components/NavBar'; 
import Coupon from '../components/Coupon';
import "../css/Reward.css"

const RewardsManager = () => {
  const [points, setPoints] = useState(0);
  const [pointsToRedeem, setPointsToRedeem] = useState("");

  const getUserIdFromJWT = () => {
    const token = localStorage.getItem("token");
    if (token) {
      const decoded = jwt_decode(token);
      return decoded.userId;
    }
    return null;
  };

  const userId = getUserIdFromJWT();

  useEffect(() => {
    const fetchPoints = async () => {
      try {
        const response = await axios.get(
          `${process.env.REACT_APP_API_URL}/points-balance?userId=${userId}`
        );
        setPoints(response.data.points);
      } catch (error) {
        console.error("Error fetching points:", error);
        alert("Failed to fetch points");
      }
    };

    if (userId) {
      fetchPoints();
    }
  }, [userId]);

  const pointArray = points.toString().split("")

  return (
    <>
      <div className="app-container">
        <header className="top-container">
          <div className="logo-points">
            <h2 className="logo">Splash</h2>
          </div>

          <div className="point-counter">
            <div className="number-container">
              <img src="/assets/PointBg1.svg" className="pbg"></img>
              <div className="point-container">
                <h2 className="pointnr">{pointArray[0]}</h2>
              </div>
            </div>
            <div className="number-container">
              <img src="/assets/PointBg2.svg" className="pbg"></img>
              <div className="point-container">
                <h2 className="pointnr">{pointArray[1]}</h2>
              </div>
            </div>
            <div className="number-container">
              <img src="/assets/PointBg3.svg" className="pbg"></img>
              <div className="point-container">
                <h2 className="pointnr">{pointArray[2]}</h2>
              </div>
            </div>
            <div className="number-container">
              <img src="/assets/PointBg4.svg" className="pbg"></img>
              <div className="point-container">
                <img src="/assets/droplet.svg" className="spointnr"></img>
              </div>
            </div>
          </div>
        </header>

        <div className="reward-container">
          <p className="txt medium">punten inwisselen:</p>
          <div><Coupon pointsNeeded={200} pointsCollected={200} /></div>
          <div><Coupon pointsNeeded={200} pointsCollected={200} /></div>
          <div><Coupon pointsNeeded={200} pointsCollected={150} /></div>
          <div><Coupon pointsNeeded={200} pointsCollected={150} /></div>
          <div><Coupon pointsNeeded={200} pointsCollected={150} /></div>
          <div><Coupon pointsNeeded={200} pointsCollected={150} /></div>
        </div>
        <NavBar />
      </div>
    </>
  );
};

export default RewardsManager;
